

































































































  import drawQrcode from '../../common/qrcode.min.js'
  
	export default {
		data() {
			return {
				qrcode: '', // 平台二维码
        author: '',
        wechatFlag: false,
        wechatStr: '',
        wechatQrCode: '',
        essayContent: null,
        qrcodeUrl: ''
			}
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      userInfo() {
        return this.$store.state.userInfo
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      },
      isAuthor() {
        return this.author
      }
    },
		onLoad(query) {
			this.qrcode = this.config.wx_qr_code
      this.author = this.userInfo.id
      this.$http({
        url: this.$apis.articleInfo,
        method: 'GET',
        data: {
          id: query.aId,
          uid: this.author
        },
        cb: (err, data) => {
          if (!err && data.success === true) {
            this.essayContent = data.data
            this.essayContent.content = this.essayContent.content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
            // 生成二维码
            if (this.isAuthor) {
              let url = this.$constant.shareUrl + '?pid=' + this.author
              this.createQrcode(url, 364, (res) => {
                this.qrcodeUrl = res
              })
            } else {
              this.qrcodeUrl = data.data.qr_code
            }
          } else {
            uni.showToast({
            	title: '获取文章详情失败',
              icon: 'none'
            })
          }
        }
      })
		},
		methods: {
      createQrcode(url, w, cb) {
        drawQrcode({
          width: uni.upx2px(w),
          height: uni.upx2px(w),
          canvasId: 'qrcodeCanvas',
          text: url
        })
        setTimeout(() => {
          uni.canvasToTempFilePath({
          	canvasId: 'qrcodeCanvas',
            success: res => {
            	if (typeof cb === 'function') {
            	  cb(res.tempFilePath)
            	}
            }
          })
        }, 300)
      },
      showWechat(str, url) {
        if (!str && !url) return
        this.wechatStr = str
        this.wechatQrCode = url || ''
        this.wechatFlag = true
      },
      hideWechat() {
        this.wechatFlag = false
      },
      call(num) {
        if (!num) {
          uni.showToast({
          	title: '用户未填写手机号码',
            icon: 'none'
          })
        } else {
          uni.makePhoneCall({
          	phoneNumber: num
          })
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '微信已复制，请打开微信查找添加',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideWechat()
          }
        })
      }
		}
	}
